<template>
  <div style="padding: 10px">

    <!--  主体表格-->
    <el-table :data="tableData" border stripe style="width: 100%">
      <el-table-column prop="type" label="品种" > </el-table-column>
      <el-table-column prop="time" label="时间" sortable > </el-table-column>
      <el-table-column prop="address" label="地点"> </el-table-column>
      <el-table-column fixed="right" label="操作" width="120">
        <template #default="scope">
          <el-popconfirm title="是否取消预约?" @confirm="handleRefund">
            <template #reference>
              <el-button type="text">取消预约</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <!--<el-button>确认预约</el-button>-->
  </div>
</template>

<script>
import request from "../../utils/request";
import account from "../../utils/account";

export default {
  data() {
    return {
      search:'',
      userId: 1,
      pageSize: 10,
      currentPage: 1,
      tableData: [
        {
          type: '狂犬疫苗',
          time: '2022-5-8',
          address: '梅园食堂',
        },
      ],
    }
  },
  created() {
    if (account.role !== "user") {
      this.$message(
          {
            type: "error",
            message: "尚未以用户身份登录"
          }
      );
      this.$router.push("/login");
    }
    this.load();
  },
  methods:{
    handleRefund(){
      request.put("/api/user/refund", {}, {
        params: {
          userId: account.id
        }
      }).then(res => {
        if (res.code === "0") {
          this.$message({
            type: "success",
            message: "取消预约成功"
          });
          this.load();
        } else {
          this.$message({
            type: "error",
            message: "取消预约失败：" + res.msg
          });
        }
      })
    },
    load() {
      request.get("/api/user/vaccine", {
        params: {
          userId: account.id
        }
      }).then(res => {
        this.tableData = [res.data];
        this.total = res.data.total;
      })
    }
  },
  name: "MineView"
}
</script>

