<template>
  <div style="width: 500px; margin: 150px auto; border-radius: 4px; border: solid 1px; text-align: -webkit-center">
    <div style="text-align: center; padding: 30px; font-size: 20px">注册新用户</div>
    <el-form
        label-position="right"
        label-width="120px"
        style="max-width: 400px"
        size="normal"
        :rules="this.rules"
        ref="form"
        :model="form"
    >
      <el-form-item label="身份证号" required prop="idcard">
        <el-input v-model="form.idcard" placeholder="身份证号" />
      </el-form-item>
      <el-form-item label="姓名" required prop="name">
        <el-input v-model="form.name" placeholder="姓名"/>
      </el-form-item>
      <el-form-item label="密码" required prop="password">
        <el-input v-model="form.password" placeholder="密码" show-password/>
      </el-form-item>
      <el-form-item>
        <el-button style="width: 100%; color: cornflowerblue" @click="register">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import request from "../../utils/request";
import rules from "../../utils/validate";

export default {
  name: "RegisterView",
  data() {
    return {
      form: {
        idcard: '',
        name: '',
        password: ''
      },
      rules: {
        idcard: rules.idcard,
        name: rules.name,
        password: rules.password
      }
    };
  },
  methods: {
    register() {
      this.$refs['form'].validate( valid => {
        if (valid) {
          request.post("/api/user/register", this.form).then(res => {
            if (res.code === "0") {
              this.$message({
                message: "注册成功",
                type: "success"
              });
              this.$router.push("/login");
            } else {
              this.$message({
                type: "error",
                message: "注册失败：" + res.msg
              });
            }
          });
        }
      });
    }
  }
};
</script>

