<template>
   <div style="height: 50px;line-height: 50px;border-bottom: 1px solid#ccc;display: flex">
     <div style="width: 200px;padding-left: 50px;font-weight: bold;color: blue">疫苗预约</div>
     <div style="flex: 1"></div>
     <div style="width: 100px;color: blue">
       <el-dropdown>
         <span class="el-dropdown-link" style="height: 50px; line-height: 45px">
           {{ this.userData.name }}
         </span>
         <template #dropdown>
           <el-dropdown-menu>
             <el-dropdown-item @click="logout">退出登录</el-dropdown-item>
           </el-dropdown-menu>
         </template>
       </el-dropdown>
     </div>
   </div>
</template>

<script>
import account from "../../utils/account";

export default {
  name: "",
  data() {
    return {
      userData: account.data
    };
  },
  methods: {
    logout() {
      account.role = undefined;
      account.data = {};
      account.id = undefined;
      this.$router.push("/login");
    }
  }
}
</script>

