<template>
  <div style="padding: 10px">
    <!--  搜索区域-->
    <div style="margin: 10px 0">
      <el-input v-model="search" placeholder="请输入关键词" style="width: 200px" clearable/>
      <el-button type="primary" style="margin-left: 20px" @click="load">查询</el-button>
      <el-button type="primary" style="margin: 10px" @click="handleInsert">新增</el-button>
    </div>
    <!--  主体表格-->
    <el-table :data="tableData" border stripe style="width: 100%">
      <el-table-column prop="type" label="品种" > </el-table-column>
      <el-table-column prop="time" label="时间" sortable > </el-table-column>
      <el-table-column prop="address" label="地点"> </el-table-column>
      <el-table-column prop="currentp" label="当前人数" > </el-table-column>
      <el-table-column prop="totalp" label="可容纳人数" > </el-table-column>
      <el-table-column fixed="right" label="操作" width="120">
        <template #default="scope">
          <el-popconfirm title="是否确定编辑?" @confirm="handleEdit(scope.row)">
            <template #reference>
              <el-button type="text">编辑</el-button>
            </template>
          </el-popconfirm>
          <el-popconfirm title="是否确定删除?" @confirm="handleRemove(scope.row)">
            <template #reference>
              <el-button type="text">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
<!--    弹窗-->
    <el-dialog
        v-model="modifyDialog.visible"
        title="修改疫苗信息"
        width="30%">
      <el-form border stripe style="width: 100%" label-width="100px">
        <el-form-item label="ID">
          <el-input v-model="modifyDialog.dialogData.id" disabled="true"/>
        </el-form-item>
        <el-form-item label="品种">
          <el-input v-model="modifyDialog.dialogData.type"/>
        </el-form-item>
        <el-form-item label="时间">
          <el-date-picker type="date" value-format="YYYY-MM-DD" v-model="modifyDialog.dialogData.time"/>
        </el-form-item>
        <el-form-item label="地点">
          <el-input v-model="modifyDialog.dialogData.address"/>
        </el-form-item>
        <el-form-item label="可容纳人数">
          <el-input v-model="modifyDialog.dialogData.totalp"/>
        </el-form-item>
        <el-form-item style="child-align: right">
          <el-button @click="modifyDialog.visible=false">取消</el-button>
          <el-button @click="handleEditFinish">确认</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog
        v-model="insertDialog.visible"
        title="新增疫苗"
        width="30%">
      <el-form border stripe style="width: 100%" label-width="100px">
        <el-form-item label="品种">
          <el-input v-model="insertDialog.dialogData.type"/>
        </el-form-item>
        <el-form-item label="时间">
          <el-date-picker type="date" value-format="YYYY-MM-DD" v-model="insertDialog.dialogData.time"/>
        </el-form-item>
        <el-form-item label="地点">
          <el-input v-model="insertDialog.dialogData.address"/>
        </el-form-item>
        <el-form-item label="可容纳人数">
          <el-input v-model="insertDialog.dialogData.totalp"/>
        </el-form-item>
        <el-form-item style="child-align: right">
          <el-button @click="insertDialog.visible=false">取消</el-button>
          <el-button @click="handleInsertFinish">确认</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--<el-button>确认预约</el-button>-->
    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage"
          v-model:page-size="pagesize"
          :page-sizes="[5, 10, 20]"
          :small="small"
          :disabled="disabled"
          :background="background"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import request from "../../utils/request";
import account from "../../utils/account";

export default {
  name: "ManageView.vue",
  data() {
    return {
      search: '',
      currentPage: 1,
      total: 10,
      small: 0,
      background: 0,
      pagesize: 10,
      tableData: [],
      modifyDialog: {
        visible: false,
        dialogData: {}
      },
      insertDialog: {
        visible: false,
        dialogData: {}
      }
    };
  },
  created() {
    if (account.role !== "manager") {
      this.$message(
          {
            type: "error",
            message: "尚未以管理员身份登录"
          }
      );
      this.$router.push("/login");
    }
    this.load();
  },
  methods: {
    load() {
      request.get("/api/vaccine", {
        params: {
          pageNum: this.currentPage,
          pageSize: this.pagesize,
          search: this.search,
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.records;
        this.total = res.data.total;
      })
    },
    handleEdit(row) {
      this.modifyDialog.dialogData = JSON.parse(JSON.stringify(row));
      this.modifyDialog.visible = true;
      // this.modifyDialog.dialogData = row.get();
    },
    handleEditFinish() {
      this.modifyDialog.visible = false;
      request.put("/api/manager/vaccine",
          this.modifyDialog.dialogData,
          { params: { managerId: account.id } }
      ).then( res => {
        if (res.code === "0") {
          this.$message({
            type: "success",
            message: "修改成功"
          })
          this.load();
        } else {
          this.$message({
            type: "error",
            message: res.msg
          });
        }
      });
    },
    handleInsert() {
      this.insertDialog.dialogData = { id: '' };
      this.insertDialog.visible = true;
      // this.modifyDialog.dialogData = row.get();
    },
    handleInsertFinish() {
      this.insertDialog.visible = false;
      request.post("/api/manager/vaccine",
          this.insertDialog.dialogData,
          { params: { managerId: account.id } }
      ).then( res => {
        if (res.code === "0") {
          this.$message({
            type: "success",
            message: "新增成功"
          })
          this.load();
        } else {
          this.$message({
            type: "error",
            message: res.msg
          });
        }
      });
    },
    handleRemove(row) {
      request.delete("/api/manager/vaccine",
          { params: { managerId: account.id, vaccineId: row.id } }
      ).then( res => {
        if (res.code === "0") {
          this.$message({
            type: "success",
            message: "删除成功"
          })
          this.load();
        } else {
          this.$message({
            type: "error",
            message: res.msg
          });
        }
      });
    },
    handleSizeChange(pageSize) {
      //改变当前每页的个数触发
      this.pagesize = pageSize;
      this.load();
    },
    handleCurrentChange(pageNum) {
      //改变当前页码的触发
      this.currentPage = pageNum;
      this.load();
    },
  }
}
</script>

