<template>
  <div style="width: 500px; margin: 150px auto; border-radius: 4px; border: solid 1px; text-align: -webkit-center">
    <div style="text-align: center; padding: 30px; font-size: 20px">欢迎登录</div>
    <el-form
        label-position="right"
        label-width="120px"
        style="max-width: 400px"
        size="normal"
        ref="form"
        :rules="this.rules"
        :model="form"
    >
      <el-form-item label="角色身份">
        <el-select v-model="form.role" autocomplete="user" style="width: 100%">
          <el-option
              v-for="item in roles"
              :label="item.label"
              :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="身份证号" prop="idcard">
        <el-input v-model="form.idcard" placeholder="身份证号"/>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="form.password" placeholder="密码" show-password/>
      </el-form-item>
      <el-form-item>
        <el-button style="width: 45%; color: cornflowerblue;" @click="login">登录</el-button>
        <el-button style="width: 45%; color: cornflowerblue" @click="$router.push('/register')">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import request from "../../utils/request";
import rules from "../../utils/validate";
import account from "../../utils/account";

export default {
  name: "LoginView",
  data() {
    return {
      form: {
        role: 'user',
        idcard: '',
        password: ''
      },
      roles: [
        {
          label: "用户",
          value: "user"
        },
        {
          label: "管理员",
          value: "manager"
        }
      ],
      rules: {
        idcard: rules.idcard,
        password: rules.password
      }
    }
  },
  methods: {
    login() {
      console.log("ROLE: " + this.form.role);
      console.log("ID CARD: " + this.form.idcard);
      console.log("PASSWORD: " + this.form.password);
      this.$refs['form'].validate( valid => {
        if (valid) {
          let loginInfo = {
            idcard: this.form.idcard,
            password: this.form.password
          };
          if (this.form.role === "user") {
            request.post("/api/user/login", loginInfo).then(res => {
              if (res.code === "0") {
                this.$message({
                  type: "success",
                  message: "登录成功"
                });
                account.id = res.data.id;
                account.role = this.form.role;
                account.data = res.data;
                this.$router.push("/home");
              } else {
                this.$message({
                  type: "error",
                  message: "登录失败：" + res.msg
                });
              }
            });
          } else {
            request.post("/api/manager/login", loginInfo).then(res => {
              if (res.code === "0") {
                this.$message({
                  type: "success",
                  message: "登录成功"
                });
                account.id = res.data.id;
                account.role = this.form.role;
                account.data = res.data;
                this.$router.push("/manage");
              } else {
                this.$message({
                  type: "error",
                  message: "登录失败：" + res.msg
                });
              }
            });
          }
        }
      });
    }
  }
}
</script>

