<template>
  <div style="padding: 10px">
<!--  搜索区域-->
  <div style="margin: 10px 0">
    <el-input v-model="search" placeholder="请输入关键词" style="width: 200px" clearable/>
    <el-button type="primary" style="margin-left: 20px" @click="load">查询</el-button>
  </div>
<!--  主体表格-->
    <el-table :data="tableData" border stripe style="width: 100%">
      <el-table-column prop="type" label="品种" > </el-table-column>
      <el-table-column prop="time" label="时间" sortable > </el-table-column>
      <el-table-column prop="address" label="地点"> </el-table-column>
      <el-table-column prop="currentp" label="当前人数" > </el-table-column>
      <el-table-column prop="totalp" label="可容纳人数" > </el-table-column>
      <el-table-column fixed="right" label="操作" width="120">
        <template #default="scope">
          <el-popconfirm title="是否确定预约?" @confirm="handleAppointment(scope.row)">
            <template #reference>
              <el-button type="text">预约</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
<!--<el-button>确认预约</el-button>-->
<div style="margin: 10px 0">
  <el-pagination
      v-model:currentPage="currentPage"
      v-model:page-size="pagesize"
      :page-sizes="[5, 10, 20]"
      :small="small"
      :disabled="disabled"
      :background="background"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
  />
</div>
  </div>
</template>

<script>

import request from "../../utils/request";
import account from "../../utils/account";

export default {
  data() {
    return {
      search: '',
      currentPage: 1,
      total: 10,
      small: 0,
      background: 0,
      pagesize: 10,
      tableData: [],
    };
  },
  created() {
    if (account.role !== "user") {
      this.$message(
          {
            type: "error",
            message: "尚未以用户身份登录"
          }
      );
      this.$router.push("/login");
    }
    this.load()
  },
  methods: {
    load() {
      request.get("/api/vaccine", {
        params: {
          pageNum: this.currentPage,
          pageSize: this.pagesize,
          search: this.search,
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.records;
        this.total = res.data.total;
      })
    },
    handleAppointment(row) {
      request.put("/api/user/appoint", {}, {
        params: {
          userId: account.id,
          vaccineId: row.id
        }
      }).then(res => {
        if (res.code === "0") {
          this.$message({
            type: "success",
            message: "预约成功"
          });
          this.load();
        } else {
          this.$message({
            type: "error",
            message: "预约失败：" + res.msg
          });
        }
      });
    },
    handleSizeChange(pageSize) {
      //改变当前每页的个数触发
      this.pagesize = pageSize;
      this.load();
    },
    handleCurrentChange(pageNum) {
      //改变当前页码的触发
      this.currentPage = pageNum;
      this.load();
    },
  },
  name: 'HomeView',
  components: {}
}
</script>
